import React from "react";
import { Container, Row, Col, Tab, Nav } from "react-bootstrap";
import { Link } from "gatsby";

const CentralizedCryptoProcess = () => {
  return (
    <>
      <Row>
        <Col lg={12}>
          <div className="dao_rows_sec">
            <h2>How does a DAO work?</h2>
            <Row className="dao_dev_tabs">
              <Tab.Container id="left-tabs-example" defaultActiveKey="first">
                <Row>
                  <Col md={4} sm={12}>
                    <Nav
                      variant="pills"
                      className="flex-row flex-md-column flex-nowrap justify-content-md-center justify-content-start"
                    >
                      <Nav.Item>
                        <Nav.Link eventKey="first">
                          <span>1.</span>Smart Contracts
                        </Nav.Link>
                      </Nav.Item>
                      <Nav.Item>
                        <Nav.Link eventKey="second">
                          <span>2.</span>Proposal Submission
                        </Nav.Link>
                      </Nav.Item>
                      <Nav.Item>
                        <Nav.Link eventKey="third">
                          <span>3.</span>Voting
                        </Nav.Link>
                      </Nav.Item>
                      <Nav.Item>
                        <Nav.Link eventKey="fourth">
                          <span>4.</span>Decision Execution
                        </Nav.Link>
                      </Nav.Item>
                      <Nav.Item>
                        <Nav.Link eventKey="fifth">
                          <span>5.</span>Tokenomics
                        </Nav.Link>
                      </Nav.Item>
                    </Nav>
                  </Col>
                  <Col md={8} sm={12} className="border_tab">
                    <Tab.Content>
                      <Tab.Pane eventKey="first">
                        <p>
                          DAOs operate through smart contracts, Which are
                          self-executing pieces of code that define the rules
                          and operations of the organization.
                        </p>

                        <Link className="dao_tab_btn" to="/contact-us">
                          View Live Demo
                        </Link>
                      </Tab.Pane>
                      <Tab.Pane eventKey="second">
                        <p>
                          Members of the DAO can submit proposals suggesting
                          actions, changes, or decisions for the organization.
                        </p>
                        <Link className="dao_tab_btn" to="/contact-us">
                          View Live Demo
                        </Link>
                      </Tab.Pane>
                      <Tab.Pane eventKey="third">
                        <p>
                          Each member, typically holding governance tokens, has
                          the right to vote on proposed actions. The voting
                          power is often proportional to the number of tokens
                          held.
                        </p>
                        <Link className="dao_tab_btn" to="/contact-us">
                          View Live Demo
                        </Link>
                      </Tab.Pane>
                      <Tab.Pane eventKey="fourth">
                        <p>
                          If a proposal gathers enough support through votes,
                          the smart contract automatically executes the proposed
                          action without the need for intermediaries.
                        </p>

                        <Link className="dao_tab_btn" to="/contact-us">
                          View Live Demo
                        </Link>
                      </Tab.Pane>
                      <Tab.Pane eventKey="fifth">
                        <p>
                          Governance tokens not only enable voting but also
                          represent ownership and influence within the DAO.
                          Token holders may receive rewards or benefits based on
                          their participation.
                        </p>
                        <Link className="dao_tab_btn" to="/contact-us">
                          View Live Demo
                        </Link>
                      </Tab.Pane>
                    </Tab.Content>
                  </Col>
                </Row>
              </Tab.Container>
            </Row>
          </div>
        </Col>
      </Row>
    </>
  );
};

export default CentralizedCryptoProcess;
