import React from "react";
import { Container, Row } from "react-bootstrap";
import TalkToUsNew from "./talk-to-us-new";

const CtaBtnThree = (props) => {
  const { db, dynamicButtonText } = props;
  const defaultButtonText =
    "Unlock The Potential Of Your Organization Today With Justtry";

  return (
    <Container>
      <Row className="dao_rows dao_cta_bg">
        <div className="dao_cta_content">
          <h2
            className="text-white"
            dangerouslySetInnerHTML={{
              __html: dynamicButtonText || defaultButtonText,
            }}
          ></h2>
          <button className="cta_btn_div">
            <TalkToUsNew labe="Talk to us" db={db} />
          </button>
        </div>
      </Row>
    </Container>
  );
};

export default CtaBtnThree;
